import { MatHorizontalStepper, MatStepper } from '@angular/material/stepper';
import { ISteps } from '@interfaces';
import { OnboardingService, FranchiseService } from '@services';
import { ResponseFranchises } from '@models';
import { Component, NgZone, OnInit, ViewChild } from '@angular/core';
import { STEPPER_GLOBAL_OPTIONS } from '@angular/cdk/stepper';
@Component({
  selector: 'app-onboarding',
  templateUrl: './onboarding.component.html',
  styleUrls: ['./onboarding.component.scss'],
  providers: [
    {
      provide: STEPPER_GLOBAL_OPTIONS,
      useValue: { displayDefaultIndicatorType: true },
    },
  ],
})
export class OnboardingComponent implements OnInit {
  @ViewChild('stepper') stp: MatStepper;
  public status: ISteps;
  public isFirstStepEditable: boolean;
  public isSecondStepEditable: boolean;
  public hasOnboardingToSkipFirstStep = false;
  public isOnboardingGettingFranchisesInfo = true;

  constructor(
    private zone: NgZone,
    private onboardingService: OnboardingService,
    private franchiseService: FranchiseService
  ) {}

  public ngOnInit() {
    this.onboardingService.onboardingStatus.subscribe((response) => {
      this.status = response;
    });
    this.checkFranchiseStatus();
  }

  private checkFranchiseStatus(): void {
    this.franchiseService
      .getFranchises()
      .subscribe((responseFranchises: ResponseFranchises) => {
        if (responseFranchises.franchises.length > 0) {
          this.hasOnboardingToSkipFirstStep = true;
          this.onboardingService.setfranchiseId(
            responseFranchises.franchises[0].id
          );
        }
        this.isOnboardingGettingFranchisesInfo = false;
      });
  }

  public loaded(stepper: MatHorizontalStepper) {
    this.zone.run(() => {
      if (stepper.selectedIndex !== 0 && this.status.isFirstStepCompleted) {
        this.isFirstStepEditable = true;
      }
      if (stepper.selectedIndex !== 1 && this.status.isSecondStepCompleted) {
        this.isSecondStepEditable = true;
      }
    });
  }
}
