import { ISteps } from '@interfaces';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatStepper } from '@angular/material/stepper';
import { RestaurantService, OnboardingService } from '@services';
import {
  Component,
  NgZone,
  OnInit,
  ViewChild,
  ElementRef,
  Input,
} from '@angular/core';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators,
  ValidatorFn,
  AbstractControl,
  NgForm,
  FormGroupDirective,
} from '@angular/forms';
import { MapsAPILoader } from '@agm/core';
import { Restaurant } from '@models';
import { PhoneNumberUtil } from 'google-libphonenumber';
import { RegexEmail, RegexUrl } from '@constants';
import { google } from 'google-maps';
declare var google: google;

@Component({
  selector: 'app-second-step',
  templateUrl: './second-step.component.html',
  styleUrls: ['./second-step.component.scss'],
})
export class SecondStepComponent implements OnInit {
  public coordinatesMatcher = new CoordinatesErrorMatcher();
  public inputFlagSection: any;
  @ViewChild('searchAddress') private searchElementRef: ElementRef;
  @Input() status: ISteps;
  @Input() step: MatStepper;
  @Input() isEditing: boolean;
  @Input() hasReturnOptionToBeVisible = true;
  constructor(
    private zone: NgZone,
    private fb: FormBuilder,
    private mapsAPILoader: MapsAPILoader,
    private onboardingService: OnboardingService,
    private restaurantService: RestaurantService
  ) {}

  // Variables used in the second step
  public secondFormGroup = this.fb.group(
    {
      name: new FormControl('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(150),
      ]),
      description: new FormControl('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(220),
      ]),
      latitude: new FormControl('', Validators.required),
      longitude: new FormControl('', Validators.required),
      address: new FormControl('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(220),
      ]),
      address_extra: new FormControl('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(220),
      ]),
      dialCode: new FormControl(52, Validators.required),
      phone: new FormControl('', [
        Validators.required,
        Validators.minLength(10),
        Validators.maxLength(10),
        this.phoneNumberValidator('MX'),
      ]),
      email: new FormControl('', {
        validators: [Validators.required, Validators.pattern(RegexEmail)],
        updateOn: 'blur',
      }),
      max_offered_seats: new FormControl({ value: 1, disabled: true }, [
        Validators.required,
        Validators.min(1),
      ]),
      max_people_per_order: new FormControl({ value: 1, disabled: true }, [
        Validators.required,
        Validators.min(1),
      ]),
      max_cancel_limit_hours: new FormControl({ value: 1, disabled: true }, [
        Validators.required,
        Validators.min(1),
      ]),
      max_cancel_limit_minutes: new FormControl({ value: 0, disabled: true }, [
        Validators.required,
        Validators.min(0),
        Validators.max(59),
      ]),
      reservation_time_minutes: new FormControl({ value: 0, disabled: true }, [
        Validators.required,
        Validators.min(1),
      ]),
      billingOption: new FormControl('email', Validators.required),
      emailBilling: new FormControl('', {
        validators: [Validators.pattern(RegexEmail), Validators.required],
        updateOn: 'blur',
      }),
      websiteBilling: new FormControl('', [Validators.pattern(RegexUrl)]),
    },
    { validator: this.coordinatesValidator }
  );

  ngOnInit(): void {
    this.initLocationInput();
  }

  private initLocationInput(): void {
    this.mapsAPILoader.load().then(() => {
      const autocomplete = new google.maps.places.Autocomplete(
        this.searchElementRef.nativeElement,
        {
          types: ['address'],
        }
      );
      autocomplete.addListener('place_changed', () => {
        this.zone.run(() => {
          const place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }
          this.secondFormGroup.get('address').setValue(place.formatted_address);
          this.secondFormGroup
            .get('latitude')
            .setValue(place.geometry.location.lat());
          this.secondFormGroup
            .get('longitude')
            .setValue(place.geometry.location.lng());
        });
      });
    });
  }

  public addInitialDialCode(inltObject: any) {
    this.inputFlagSection = inltObject;
    const dialCodeElement = document.createElement('P');
    dialCodeElement.textContent = '+52';
    dialCodeElement.setAttribute('id', 'dial');
    const flagElement = this.inputFlagSection.selectedFlag.childNodes[0];
    flagElement.parentNode.insertBefore(
      dialCodeElement,
      flagElement.nextSibling
    );
  }

  private setInputDialCodeOnSelectedCountry(code: number) {
    const child = document.getElementById('dial');
    child.textContent = `+${code}`;
  }

  public setDialCode(iso: string, dialCode: number) {
    this.secondFormGroup.get('dialCode').setValue(dialCode);
    this.secondFormGroup
      .get('phone')
      .setValidators([
        Validators.required,
        Validators.minLength(10),
        Validators.maxLength(10),
        this.phoneNumberValidator(iso.toUpperCase()),
      ]);
    this.secondFormGroup.get('phone').updateValueAndValidity();
    this.setInputDialCodeOnSelectedCountry(dialCode);
  }

  private submitSecondStep() {
    const restaurantToSave = this.restaurantToSave;
    this.restaurantService
      .createRestaurant(restaurantToSave, this.onboardingService.franchiseId)
      .subscribe((restaurant: Restaurant) => {
        this.onboardingService.setRestaurantId(restaurant.id);
        this.continueToNextStep();
      });
  }

  public manageControlQuantity(
    quantity: number = 1,
    form: FormGroup,
    controlName: string,
    operation: string
  ): void {
    const actualValue = form.get(controlName).value;
    let newValue = actualValue;
    if (operation === 'subtract') {
      if (
        actualValue > quantity &&
        controlName !== 'max_cancel_limit_minutes'
      ) {
        newValue = actualValue - quantity;
      } else if (
        (controlName === 'max_cancel_limit_minutes' ||
          controlName === 'reservation_time_minutes') &&
        actualValue > 0 &&
        actualValue !== 60
      ) {
        newValue = actualValue - quantity;
      } else if (
        (controlName === 'max_cancel_limit_minutes' ||
          controlName === 'reservation_time_minutes') &&
        actualValue === 50
      ) {
        newValue = 60;
        newValue = newValue - quantity;
      }
    } else if (operation === 'add' && actualValue < 99) {
      if (
        (controlName === 'max_cancel_limit_minutes' ||
          controlName === 'reservation_time_minutes') &&
        (actualValue + quantity > 60 || actualValue + quantity === 60)
      ) {
        if (controlName === 'max_cancel_limit_minutes') {
          this.secondFormGroup
            .get('max_cancel_limit_hours')
            .setValue(
              this.secondFormGroup.get('max_cancel_limit_hours').value + 1
            );
          newValue = 0;
        } else {
          newValue = 60;
        }
      } else {
        newValue = actualValue + quantity;
      }
    }
    form.get(controlName).setValue(newValue);
  }

  private coordinatesValidator(form: FormGroup) {
    const condition =
      !!form.get('latitude').value && !!form.get('longitude').value;
    return condition ? null : { coordinatesDoNotSetted: true };
  }

  private phoneNumberValidator(regionCode: string): ValidatorFn {
    const phoneNumberUtil = PhoneNumberUtil.getInstance();
    return (control: AbstractControl): { [key: string]: any } => {
      let validNumber = false;
      try {
        const phoneNumber = phoneNumberUtil.parseAndKeepRawInput(
          control.value,
          regionCode
        );
        validNumber = phoneNumberUtil.isValidNumber(phoneNumber);
      } catch (e) {}

      return validNumber ? null : { wrongNumber: { value: control.value } };
    };
  }

  public submit(): void {
    if (this.secondFormGroup.invalid) {
      this.onboardingService.sendFieldsAlert(this.secondFormGroup);
    } else {
      this.submitStep();
    }
  }

  public submitStep(): void {
    if (this.isEditing) {
      this.editRestaurant();
    } else {
      this.submitSecondStep();
    }
  }

  public editRestaurant() {
    const payload = this.restaurantToSave;
    this.restaurantService
      .updateRestaurantDetails(
        this.onboardingService.restaurantId.toString(),
        payload
      )
      .subscribe((response) => {
        this.continueToNextStep();
      });
  }

  private continueToNextStep(): void {
    this.zone.run(() => {
      this.step.selected.completed = true;
      this.step.next();
      this.onboardingService.setStep(this.status, {
        isSecondStepCompleted: true,
      });
    });
  }

  public goBack(): void {
    this.onboardingService.setStep(this.status, {
      isFirstStepCompleted: false,
    });
    this.step.previous();
  }

  get restaurantToSave(): Restaurant {
    const body = new Restaurant({
      name: this.secondFormGroup.get('name').value,
      description: this.secondFormGroup.get('description').value,
      address: this.secondFormGroup.get('address').value,
      address_extra: this.secondFormGroup.get('address_extra').value,
      coordinates: [
        this.secondFormGroup.get('latitude').value.toString(),
        this.secondFormGroup.get('longitude').value.toString(),
      ],
      phone:
        '+' +
        this.secondFormGroup.get('dialCode').value +
        this.secondFormGroup.get('phone').value,
      email: this.secondFormGroup.get('email').value,
      max_offered_seats: this.secondFormGroup.get('max_offered_seats').value,
      max_people_per_order: this.secondFormGroup.get('max_people_per_order')
        .value,
      max_cancel_limit_minutes:
        this.secondFormGroup.get('max_cancel_limit_hours').value * 60 +
        this.secondFormGroup.get('max_cancel_limit_minutes').value,
      reservation_time_minutes: this.secondFormGroup.get(
        'reservation_time_minutes'
      ).value,
      is_head_office: false,
      invoice_email: this.secondFormGroup.value.emailBilling,
      invoice_url: this.secondFormGroup.value.websiteBilling,
    });

    if (this.secondFormGroup.value.billingOption === 'email') {
      delete body.invoice_url;
    } else if (this.secondFormGroup.value.billingOption === 'website') {
      delete body.invoice_email;
    }

    return body;
  }

  public changeForm(): void {
    if (this.secondFormGroup.controls.billingOption.value === 'email') {
      this.disableInput('websiteBilling');
      this.enableInput('emailBilling');
      this.setValidators('emailBilling');
    } else if (
      this.secondFormGroup.controls.billingOption.value === 'website'
    ) {
      this.disableInput('emailBilling');
      this.enableInput('websiteBilling');
      this.setValidators('websiteBilling');
    }
  }

  private enableInput(input: string): void {
    this.secondFormGroup.get(input).enable();
  }

  private disableInput(input: string): void {
    this.secondFormGroup.get(input).disable();
  }

  private setValidators(input: string): void {
    if (this.secondFormGroup.controls.billingOption.value === 'email') {
      this.secondFormGroup
        .get(input)
        .setValidators([Validators.required, Validators.pattern(RegexEmail)]);
      this.secondFormGroup.get('websiteBilling').setValidators([]);
    } else if (
      this.secondFormGroup.controls.billingOption.value === 'website'
    ) {
      this.secondFormGroup
        .get(input)
        .setValidators([Validators.required, Validators.pattern(RegexUrl)]);
      this.secondFormGroup.get('emailBilling').setValidators([]);
    }
  }

  public selectedBillingOption(value: string): boolean {
    return this.secondFormGroup.get('billingOption').value === value;
  }
}

class CoordinatesErrorMatcher implements ErrorStateMatcher {
  isErrorState(
    control: FormControl | null,
    form: FormGroupDirective | NgForm | null
  ): boolean {
    return (
      (form.submitted || control.touched) &&
      form.hasError('coordinatesDoNotSetted')
    );
  }
}
