import { ISteps } from '@interfaces';
import { BehaviorSubject } from 'rxjs';
import { FormGroup } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Injectable } from '@angular/core';
@Injectable({
  providedIn: 'root',
})
export class OnboardingService {
  public status = new BehaviorSubject<ISteps>({
    isFirstStepCompleted: false,
    isSecondStepCompleted: false,
    isThirdStepCompleted: false,
    isFourthStepCompleted: false,
    isFifthStepCompleted: false,
  });

  public onboardingStatus = this.status.asObservable();

  constructor(public snackBar: MatSnackBar) {}

  public setStep(step: ISteps, data: any) {
    const nextStep = Object.assign(step, data);
    this.status.next(nextStep);
  }

  /**
   * Sends an alert if the form that is being submitted is invalid
   *
   * @param form FormGroup to be checked
   */
  public sendFieldsAlert(form: FormGroup): void {
    if (form.invalid) {
      this.snackBar.open(
        'Por favor, revisa los campos que estÃ¡n marcados en rojo',
        'Entendido',
        {
          duration: 15000,
        }
      );
    }
  }

  public setRestaurantId(restaurantId: number): void {
    localStorage.setItem('restaurantId', JSON.stringify(restaurantId));
  }

  public setfranchiseId(franchiseId: number): void {
    localStorage.setItem('franchiseId', JSON.stringify(franchiseId));
  }

  public get franchiseId(): number {
    return JSON.parse(localStorage.getItem('franchiseId'));
  }

  public get restaurantId(): number {
    return JSON.parse(localStorage.getItem('restaurantId'));
  }
}
