import { FormControl, Validators } from '@angular/forms';

import { ISteps } from '@interfaces';
import { OnboardingService, RestaurantService } from '@services';
import { AmenitiesPayload } from '@payloads';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { Component, OnInit, Input, NgZone } from '@angular/core';
import { Amenities, Amenity, Extra, AmenitiesToSave } from '@models';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatStepper } from '@angular/material/stepper';

@Component({
  selector: 'app-third-step',
  templateUrl: './third-step.component.html',
  styleUrls: ['./third-step.component.scss'],
})
export class ThirdStepComponent implements OnInit {
  @Input() status: ISteps;
  @Input() step: MatStepper;
  // Variables used in the third step
  public typesListAmenities: Array<Amenity> = [];
  public newExtraControl = new FormControl(
    '',
    Validators.compose([Validators.minLength(3), Validators.maxLength(80)])
  );
  public zonesListAmenities: Array<Amenity> = [];
  public extrasListAmenities: Array<Extra> = [];
  public parkingsListAmenities: Array<Amenity> = [];
  public paymentListAmenities: Array<Amenity> = [];
  public activeNewService = false;
  private zonesList: Array<number> = [];
  private typesList: Array<number> = [];
  private paymentMethodsList: Array<number> = [];
  private parkingsList: Array<number> = [];
  private extrasList: Array<number> = [];
  public extrasAddedByUser: Array<string> = [];
  private extrasAddedByUserToSave: Array<string> = [];
  public shouldSaveAmenities = false;

  constructor(
    private restaurantService: RestaurantService,
    private snackBar: MatSnackBar,
    private onboardingService: OnboardingService,
    private zone: NgZone
  ) {}

  ngOnInit(): void {
    this.getAmenities();
  }

  private getAmenities() {
    this.restaurantService.getAmenities().subscribe((amenities: Amenities) => {
      this.typesListAmenities = amenities.types;
      this.zonesListAmenities = amenities.zones;
      this.extrasListAmenities = amenities.extras.map(
        (extra) => new Extra(extra)
      );
      this.parkingsListAmenities = amenities.parkings;
      this.paymentListAmenities = amenities.payment_methods;
    });
  }

  public addUserExtraAmenity() {
    if (this.newExtraControl.invalid) {
      return;
    }
    const { value } = this.newExtraControl;

    for (const extra of this.extrasListAmenities) {
      if (extra.name.toLowerCase() === value.toLowerCase()) {
        this.snackBar.open('El nombre ya estÃ¡ en uso', 'Entendido', {
          duration: 15000,
        });

        return;
      }
    }

    const extraObject = new Extra({
      name: this.capitalizeString(value),
      active: true,
      is_global: false,
    });

    this.extrasListAmenities.push(extraObject);
    this.activeNewService = false;
    this.newExtraControl.setValue('');
  }

  public onExtraCheck(event: MatCheckboxChange, index: number) {
    this.extrasListAmenities[index].active = event.checked;
  }

  private capitalizeString(value: string): string {
    const splitString = value.toLowerCase().split(' ');

    for (let i = 0; i < splitString.length; i++) {
      splitString[i] =
        splitString[i].charAt(0).toUpperCase() + splitString[i].substring(1);
    }

    return splitString.join(' ');
  }

  public switchUserExtraAmenitiesStatus(name: string) {
    const index = this.extrasAddedByUserToSave.indexOf(name);
    if (index !== -1) {
      this.extrasAddedByUserToSave.splice(index, 1);
    } else {
      this.extrasAddedByUserToSave.push(name);
    }
  }

  public switchAmenitiesStatus(id: number, controlName: string) {
    if (controlName === 'type') {
      const index = this.typesList.indexOf(id);
      if (index !== -1) {
        this.typesList.splice(index, 1);
      } else {
        this.typesList.push(id);
      }
    }
    if (controlName === 'zone') {
      const index = this.zonesList.indexOf(id);
      if (index !== -1) {
        this.zonesList.splice(index, 1);
      } else {
        this.zonesList.push(id);
      }
    }
    if (controlName === 'parking') {
      const index = this.extrasList.indexOf(id);
      if (index !== -1) {
        this.parkingsList.splice(index, 1);
      } else {
        this.parkingsList.push(id);
      }
    }
    if (controlName === 'payment') {
      const index = this.extrasList.indexOf(id);
      if (index !== -1) {
        this.paymentMethodsList.splice(index, 1);
      } else {
        this.paymentMethodsList.push(id);
      }
    }
  }

  public submit(): void {
    const amenitiesPayload: AmenitiesToSave = new AmenitiesPayload(
      this.typesList,
      this.zonesList,
      this.extrasListAmenities,
      this.parkingsList,
      this.paymentMethodsList
    ).toJson();

    this.restaurantService
      .setRestaurantAmenities(
        amenitiesPayload,
        this.onboardingService.restaurantId
      )
      .subscribe((response) => {
        this.continueToNextStep();
      });
  }

  public deleteAmenity(index: number): void {
    this.extrasListAmenities.splice(index, 1);
  }

  public declineToSaveAmenities(): void {
    this.shouldSaveAmenities = false;
    this.continueToNextStep();
  }

  private continueToNextStep(): void {
    this.zone.run(() => {
      this.step.selected.completed = true;
      this.step.next();
      this.onboardingService.setStep(this.status, {
        isThirdStepCompleted: true,
      });
    });
  }

  public goBack(): void {
    this.step.previous();
    this.onboardingService.setStep(this.status, {
      isSecondStepCompleted: false,
    });
  }
}
