import { MatStepper } from '@angular/material/stepper';
import { ISteps } from '@interfaces';
import { Router } from '@angular/router';
import { RestaurantService, OnboardingService } from '@services';
import { GalleryImage } from '@models';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Component, OnInit, Input } from '@angular/core';
import { RegexUrl } from '@constants';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators,
  FormArray,
} from '@angular/forms';

@Component({
  selector: 'app-fifth-step',
  templateUrl: './fifth-step.component.html',
  styleUrls: ['./fifth-step.component.scss'],
})
export class FifthStepComponent implements OnInit {
  @Input() status: ISteps;
  @Input() step: MatStepper;
  public coverImage = '';
  public galleryImages: FormArray;
  public brandImages: Array<any> = [];
  public fifthFormGroup: FormGroup;
  constructor(
    private fb: FormBuilder,
    private snackBar: MatSnackBar,
    private onboardingService: OnboardingService,
    private restaurantService: RestaurantService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.initForm();
  }

  public initForm() {
    this.fifthFormGroup = this.fb.group({
      coverImage: new FormControl(null, Validators.required),
      imageLink: new FormControl('', Validators.pattern(RegexUrl)),
      galleryImages: this.fb.array([]),
      galleryImageInput: new FormControl(),
    });
  }

  public setImageValueToGallery(e: any, controlName: string) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    const fileSize = file.size;
    const fileType = file.type;
    if (fileSize > 1048576) {
      this.snackBar.open('La imagen excede 1MB', 'Entendido', {
        duration: 15000,
      });
      this.fifthFormGroup.get('galleryImageInput').setValue(null);
      return;
    }
    if (
      fileType !== 'image/png' &&
      fileType !== 'image/jpeg' &&
      fileType !== 'image/jpg'
    ) {
      this.snackBar.open('El archivo no es de un tipo permitido', 'Entendido', {
        duration: 15000,
      });
      return;
    }
    const reader = new FileReader();
    reader.onload = (eImage: any) => {
      const img = new Image();
      img.onload = () => {
        if (controlName === 'coverImage') {
          this.fifthFormGroup.get(controlName).setValue(file);
          this.coverImage = img.src;
        } else if (controlName === 'gallery') {
          if (this.galleryImagesAsFormArray.length < 10) {
            const imageAux: GalleryImage = {
              src: img.src,
              fileValue: file,
              id: null,
              position: this.galleryImagesAsFormArray.length + 1,
            };
            this.addGalleryImage(imageAux);
            if (this.galleryImagesAsFormArray.length === 10) {
              this.fifthFormGroup.get('galleryImageInput').disable();
            }
          } else {
            this.snackBar.open(
              'Has alcanzado el limite de 10 imÃ¡genes',
              'Entendido',
              {
                duration: 15000,
              }
            );
          }
          this.fifthFormGroup.get('galleryImageInput').setValue(null);
        }
      };
      img.src = eImage.target.result;
    };
    reader.readAsDataURL(file);
  }

  private createGalleryImage(galleryImage: GalleryImage): FormGroup {
    return this.fb.group({
      fileValue: [galleryImage.fileValue, []],
      src: [galleryImage.src, []],
    });
  }

  public removeGalleryImage(imageIndex: number) {
    this.galleryImages = this.galleryImagesAsFormArray;
    this.galleryImages.removeAt(imageIndex);
    this.fifthFormGroup.get('galleryImageInput').enable();
  }

  public get galleryImagesAsFormArray() {
    return this.fifthFormGroup.get('galleryImages') as FormArray;
  }

  private addGalleryImage(galleryImage: GalleryImage): void {
    this.galleryImagesAsFormArray.push(this.createGalleryImage(galleryImage));
  }

  private submitFifthtStep() {
    const galleryToSave: FormData = this.getGallerytoSave();
    this.restaurantService
      .setRestaurantGallery(galleryToSave, this.onboardingService.restaurantId)
      .subscribe(() => {
        this.router.navigate(['/admin/menu']);
      });
  }

  public declineToStoreImages(): void {
    this.router.navigate(['/admin/menu']);
  }

  private getGallerytoSave(): FormData {
    const galleryToSave = new FormData();
    galleryToSave.append('cover', this.fifthFormGroup.get('coverImage').value);
    for (let i = 0; i < this.galleryImagesAsFormArray.controls.length; i++) {
      galleryToSave.append(
        'gallery[' + i + ']',
        this.galleryImagesAsFormArray.controls[i].get('fileValue').value
      );
    }
    if (this.fifthFormGroup.get('imageLink').value) {
      galleryToSave.append(
        'matterport',
        this.fifthFormGroup.get('imageLink').value
      );
    }
    return galleryToSave;
  }

  public submit(): void {
    if (this.fifthFormGroup.invalid) {
      this.onboardingService.sendFieldsAlert(this.fifthFormGroup);
    } else {
      this.submitFifthtStep();
    }
  }

  public goBack(): void {
    this.onboardingService.setStep(this.status, {
      isFourthStepCompleted: false,
    });
    this.step.previous();
  }
}
