import { IFranchise, ISteps } from '@interfaces';
import { MatHorizontalStepper } from '@angular/material/stepper';
import { OnboardingService } from '@services';
import {
  Franchise,
  ResponseStyles,
  Style,
  PriceRange,
  ResponsePriceRanges,
} from '@models';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators,
} from '@angular/forms';
import { FranchiseService } from '@services';
import * as firebase from 'firebase/app';
import { Component, OnInit, Input, NgZone } from '@angular/core';

@Component({
  selector: 'app-first-step',
  templateUrl: './first-step.component.html',
  styleUrls: ['./first-step.component.scss'],
})
export class FirstStepComponent implements OnInit {
  @Input() status: ISteps;
  @Input() isEditing: boolean;
  @Input() step: MatHorizontalStepper;
  public firstFormGroup: FormGroup;
  public styleList: Array<Style> = [];
  public priceRangesList: Array<PriceRange> = [];
  public userName = firebase.auth().currentUser.displayName;

  constructor(
    private zone: NgZone,
    private fb: FormBuilder,
    private franchiseService: FranchiseService,
    private onboardingService: OnboardingService
  ) {}

  ngOnInit() {
    this.createForm();
    this.getStyles();
    this.getPriceRanges();
  }

  private getStyles() {
    this.franchiseService.getStyles().subscribe((response: ResponseStyles) => {
      this.styleList = response.styles;
    });
  }

  public createForm(): void {
    this.firstFormGroup = this.fb.group({
      name: new FormControl('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(150),
      ]),
      price_range: new FormControl('', Validators.required),
      style: new FormControl('', Validators.required),
    });
  }

  private createFranchise() {
    const franchiseToSave = {
      name: this.firstFormGroup.get('name').value,
      style: this.firstFormGroup.get('style').value,
      price_range: this.firstFormGroup.get('price_range').value,
    };

    this.franchiseService
      .createFranchise(franchiseToSave)
      .subscribe((franchise: Franchise) => {
        this.onboardingService.setfranchiseId(franchise.id);
        this.continueToNextStep();
      });
  }

  private getPriceRanges(): void {
    this.franchiseService
      .getPriceRanges()
      .subscribe((response: ResponsePriceRanges) => {
        this.priceRangesList = response.price_ranges;
      });
  }

  public submit(): void {
    if (this.firstFormGroup.invalid) {
      this.onboardingService.sendFieldsAlert(this.firstFormGroup);
    } else {
      this.submitFirstStep();
    }
  }

  public submitFirstStep(): void {
    if (this.isEditing) {
      this.editFranchise();
    } else {
      this.createFranchise();
    }
  }

  public editFranchise(): void {
    const payload = this.createFranchisePutBody();
    this.franchiseService
      .updateFranchise(this.onboardingService.franchiseId, payload)
      .subscribe(() => {
        this.continueToNextStep();
      });
  }

  private createFranchisePutBody(): Franchise {
    const { name, price_range, style } = this.firstFormGroup.value;
    const body: IFranchise = {
      id: this.onboardingService.franchiseId,
      name,
      price_range,
      style,
    };

    return body;
  }

  private continueToNextStep(): void {
    this.zone.run(() => {
      this.step.selected.completed = true;
      this.step.next();
      this.onboardingService.setStep(this.status, {
        isFirstStepCompleted: true,
      });
    });
  }
}
